import CardGenerator from '../../../common_ui/card_generator';

import VideoService from '../../../services/video.service';
import ClacDuration from '../../../utils/ClacDuration';
import PrettyDate from '../../../utils/PrettyDate';

const CONTENTS_PER_PAGE = 20;

export default (() => {
    let $video_list = null;
    let $total_count = null;
    let listData = [];
    let $load_contents_spinner = null;
    let _start = 0;
    let _contentsEnd = false;
    let _userSeq = 0;

    
    const init = (userSeq) => {
        $video_list = $('.card-list');
        $total_count = $('#lbl-total .count');
        $load_contents_spinner = $('.spinner');
        _userSeq = userSeq;

    }

    const getList = async (order, init) => {
        
        if (init) {
            $video_list.empty();
            listData = [];
            _contentsEnd = false;
            _start = 0;
        }

        if (_contentsEnd) return { state: false, status: "contents_end" };

        return new Promise(async (resolve, reject) => {
            $load_contents_spinner.show();

            const resMyVideoList = await VideoService.getVideoList(_start, CONTENTS_PER_PAGE, order, _userSeq);
            const totalCount = resMyVideoList.data.cnt;
            const vods = resMyVideoList.data.vods;

            $total_count.text(totalCount);

            listData.push(...vods);

            vods.map((vod) => {
                const { title, nick_name, profile_img_url, thumbnail_path, vod_seq, duration, view_cnt, created_at } = vod;
                const combinedPath = thumbnail_path;
                const url = `/video/${vod_seq}`;
                $video_list.append(
                    CardGenerator.createCard({
                        title,
                        name: nick_name,
                        avatar_url: profile_img_url,
                        thumbnail: combinedPath,
                        url,
                        view_cnt,
                        duration: ClacDuration(duration),
                        update: PrettyDate(created_at)
                    })
                );
            })

            bindEventListItems();

            if (vods.length < CONTENTS_PER_PAGE) {
                _contentsEnd = true;
                $load_contents_spinner.hide();
                resolve({ state: false, status: "contents_end" });
            }

            _start = _start + CONTENTS_PER_PAGE;

            resolve({ state: true, status: "get_contents" });
        })
    }

    const bindEventListItems = () => {
        $('.card .title-container, .card .thumbnail-wrapper').off('click');
        $('.card .title-container, .card .thumbnail-wrapper').click((e) => {
            e.preventDefault();
            const itemIndex = $('.card').index($(e.currentTarget).parent());
            location.href = `/video/${listData[itemIndex].vod_seq}`;
        })

        $('.card .name-container').off('click');
        $('.card .name-container').click( (e) => {
            e.preventDefault();
            const itemIndex = $('.card').index($(e.currentTarget).closest('.card'));
            location.href = `/channel/${listData[itemIndex].user_seq}`;
        })
    }

    return {
        init, getList
    }
})()

