// ui
import DropDownMenu from '../../common_ui/drop_down_menu';
import uiListPanel from './ui_components/video_list.ui.card_list_panel';
import { getParameterByName } from '../../utils/ParameterParser';
import CommonService from '../../services/common.service';
const URL_PARAM_USER_SEQ = 'user_seq';

let uiSortMenu = null;
let _order = null;
const ORDER_CODE = [
    "01", "02", "03"
]
let uiEmptyMsg = null;

let scrollPaging = true;
let _pending = false;

$(document).ready( async () => {
    uiSortMenu = new DropDownMenu();
    uiSortMenu.init('#sort-menu');
    uiSortMenu.attachChangeHandler(onChangeSort);
    _order = ORDER_CODE[0];

    uiEmptyMsg = $('.msg-empty-contents');
    uiEmptyMsg.hide();

    let userSeq = 0;
    if (getParameterByName(URL_PARAM_USER_SEQ)) {
        userSeq = getParameterByName(URL_PARAM_USER_SEQ)
        getUserInfo(userSeq)
    }
    uiListPanel.init(userSeq);

    bindEventHandler();

    getUserInfo(userSeq);

    loadList(_order, true);
});

const getUserInfo = (userSeq) => {
    if(userSeq === 0) return;
    CommonService.getUserInfo(userSeq)
    .then( res => {
        $('.title-container .title').text(` > ${res.data.nick_name}`)
    })
}

const loadList = async (order, init) => {
    try {
        _pending = true;
        const oldTime = new Date();
        const { status } = await uiListPanel.getList(order, init);
        const diff = new Date() - oldTime;
        _pending = false;
        return status;
    } catch(err) {
        _pending = false;
        console.log(err)
    }

}

const onChangeSort = async (typeIndex) => {
    _order = ORDER_CODE[typeIndex];
    scrollPaging = true;
    loadList(_order, true);
}

const bindEventHandler = () => {
    $(window).scroll( async (e) => {
        if (scrollPaging) {
            if ($(window).scrollTop() >= ($(document).height() - $(window).height()) - 5) {
                if (_pending) {
                    return;
                }
                const status = await loadList(_order);
                if (status === "contents_end") {
                    scrollPaging = false;
                }
            }
        }
    })
}
